
























import {Component, Vue} from 'vue-property-decorator';
import {Button, Overlay} from 'vant';
import CertShow from '@/components/cert_card.vue';

import 'vant/lib/row/style';
import 'vant/lib/col/style';
import 'vant/lib/button/style';
import 'vant/lib/overlay/style';
import {notes, NoteType} from '@/util/note';
import {DiamondUtil, Status} from '../../util/diamond_util';
import {CertOwn, CertSignStatus} from '../../model/certs_model';
import {CashboxJs} from '@/util/cashboxjs';
import {ExtrinsicStatus} from "@polkadot/types/interfaces";
import {ToastUtil} from "../../util/toast";

Vue.use(Button).use(Overlay);

@Component({
    components: {CertShow},
})
export default class Confirm extends Vue {
    private certOwn!: CertOwn;
    private certSignStateText!: string;
    private showUpChainBtn = true;
    private isShowLoading = false;

    public created(): void {
        this.certOwn = this.$store.state.nowCertOwn;
        this.$store.state.title = "信息上链";
        if (this.$store.state.nowCertOwn.attachInfo.certSignStatus === CertSignStatus.Success) {
            this.certSignStateText = "签名成功";
            this.showUpChainBtn = true;
        } else {
            this.certSignStateText = "签名失败";
            ToastUtil.fail("证书签名失败！建议您重新检查流程中的问题，先不提交证书")
        }
    }

    public onClickDConfirm(): void {
        this.isShowLoading = true;
        DiamondUtil.getInstance().sendToChain(this.$store.state.nowCertOwn.attachInfo.certSignedInfo, (status: ExtrinsicStatus) => {
            if (status.isFinalized) {//完成
                console.log("use balance is finished!===>");
                let txHash = status.asFinalized;
                DiamondUtil.getInstance().loadApiPromise().then((apiPromise) => {
                    apiPromise.query.system.events.at(txHash).then((events) => {
                        this.isShowLoading = false;
                        let isExecSuccess = false;
                        events.forEach(({phase, event: {data, method, section}}) => {
                            if (section == "contracts" && method == "ContractExecution") {
                                console.log("contracts===>" + data.toJSON());
                                ToastUtil.show("信息上链已完成，可在浏览器查询交易状态", 8000);
                                isExecSuccess = true;
                            }
                            console.log('forEach  =>' + phase.toString() + "||section=>" + `: ${section}.${method}` + data.toString());
                        });
                        if (!isExecSuccess) {
                            ToastUtil.fail("信息上链失败，链上调用出现问题！~", 8000);
                        }
                    });
                });
            }
            if (status.isBroadcast) {//广播
                console.log("status.isBroadcast===>");
                console.log("use balance is finished!===>");
                ToastUtil.show("信息上链已完成，可在浏览器查询交易状态");
            }
            if (status.isDropped) {//丢弃
                console.log("status.isDropped===>");
                ToastUtil.fail("信息上链失败，操作已被丢弃");
                return;
            }
            if (status.isInvalid) {//无效
                console.log("status.isInvalid!===>");
                ToastUtil.fail("信息上链失败，无效操作");
                return;
            }
            let tThis = this;
            this.isShowLoading = false;
            setTimeout(function () {
                //路由跳转
                tThis.$router.replace('/frame/cert/process');
            }, 1000);
        }).catch((reason) => {
            this.isShowLoading = false;
            console.log("上链出现错误,reason:" + reason);
            ToastUtil.show("上链出现错误，" + reason);
            return;
        });
    }
}
