












import {Component, Vue, Inject, Prop} from 'vue-property-decorator';
import {CertOwn} from '../model/certs_model';
import {Divider} from 'vant'
import 'vant/lib/divider/style';

Vue.use(Divider);

@Component({name:'CertCard'})
export default class CertCard extends Vue {
    private certOwn!: CertOwn;

    // 处理证书中的多语言
    public get certMap(): Array<{ key: string, value: string }> {
        if (!this.certOwn) {
            this.certOwn = this.$store.state.nowCertOwn;
        }
        const mapI18n = [];
        if (this.certOwn && this.certOwn.certGia) {
            mapI18n.push(...this.certOwn.certGia.toCert().map);
            if (this.certOwn.certGia.toCert().i18n) {
                for (const it of this.certOwn.certGia.toCert().map.keys()) {
                    const item = this.certOwn.certGia.toCert().map[it];
                    const l = this.$t(item.key) as string;
                    if (l) {
                        item.key = l;
                        mapI18n[it] = item;
                    }
                }
            }
        }
        return mapI18n;
    }
}
